.home-container {
  width: 100%;
  height: 100%;
  color: white;
  background-color: hsla(30, 5%, 5%, 0);
  /* background-image: linear-gradient(135deg, hsl(45, 15%, 30%), hsl(45, 10%, 30%), hsl(45, 5%, 15%)); */
  /* background-image: url("../assets/360_F_294992494_kPG2PgIe6Jn9sU4OcSw6Th6SvZD4OfWy.jpg"); */
  background-size: 100% 80%;
}
.background-image {
  position: fixed;
  z-index: -1;
  top: -20vw;
  left: 0;
  width: 100vw;
}

#home-box {
  display: block;
  width: 75%;
  height: 100vh;
  background-color: hsla(30, 5%, 25%,0);
  margin: auto;
  padding: 0px;
  text-shadow: 1px 1px 2px hsl(30, 5%, 25%), 1px 1px 2px hsl(30, 5%, 25%);
}
h1 {
  font-size: 48px;
}
#boyang-photo-1 {
  border: 1px solid hsl(45, 75%, 50%);
}
.top-notes {
  font-size: 14px;
}
.intro-text {
  font-size: 21px;
}


@media screen and (orientation: portrait) {
  h1 {
    font-size: 8vw;
  }
  .background-image {
    height: 125vh;
    width: auto;
    left: auto;
    right: 0%;
    top: -25vh;
  }
  #boyang-photo-1 {
    width: 50vw;
  }
  #home-box {
    height: 125vh;
  }
  .top-notes {
    font-size: 12px;
  }
  .intro-text {
    font-size: 19px;
  }
}